import Vue from 'vue';
import Component from 'vue-class-component';
import { Action, State } from 'vuex-class';

import flatButton from 'muse-ui/flatButton';

import Api from '~/api';
import { ToastMsg } from '~/entity/toast';
import { IAddFriendItem } from '~/entity/friend/Ifriend';
import { Ifriend } from 'src/entity/friend';
import { IPersonalParams } from 'src/entity/route_params';

@Component({
    components: {
        'mu-flat-button': flatButton
    }
})
export default class FriendHistoryComp extends Vue {

    addFriendList: IAddFriendItem[] = null;

    /* 刷新 */
    refreshingFriends = false;

    friendsContainer: HTMLElement = null;

    @Action('showToast')
    showToast;

    @Action('addFriend')
    addFriend;

    $refs: {
        friendsContainer: any
    };  
    
    mounted() {
        this.friendsContainer = this.$refs.friendsContainer;
    }

    activated() {
        this.refreshFriend();
    }

    async refreshFriend() {
        this.refreshingFriends = true;
        try {
            const res = await Api.getAddFriendList();
            this.addFriendList = res.body.obj;
            console.log(res.body.obj); 
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        } finally {
            this.refreshingFriends = false;
        }
    }

    /**
     * 添加一个好友
     */
    async acceptFriend(addFriendItem: IAddFriendItem) {
        try {
            const res = await Api.acceptFriend(1, addFriendItem.sender);
            addFriendItem.recv = 1;
            const friend: Ifriend = res.body.obj;

            // 更新好友列表，如果对方已经是好友，则返回null
            friend && this.addFriend(friend);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        }
    }

    toPersonalPage(addFriend: IAddFriendItem) {
        if (addFriend.recv === 0) return;
        const params: IPersonalParams = {
            action: 'newFriend',
            id: addFriend.sender
        };
        this['$router'].push({
            name: 'personalPage',
            params
        }); 
    }
}



